





















import { Vue, Component } from 'vue-property-decorator'
import Types from '../../../../types'
import hljs from 'highlight.js'

@Component
export default class articleDetails extends Vue {
  articleId: number
  articleInfo = {}
  loading: boolean = true

  async mounted() {
    this.articleId = this.$lo.get(this.$route, 'query.id')
    this.articleInfo = await this.getArticleDetails().then(
      (req: Types.InterfaceData) => this.$util.checkResp(req)
    )
    this.$nextTick(() => {
      const codes = document.querySelectorAll('.card-body pre code')
      codes.forEach((elem) => {
        hljs.highlightBlock(elem)
      })
    })
    setTimeout(() => {
      this.loading = false
    }, 1000)
  }

  getArticleDetails() {
    return this.$api.getArticleDetails({ id: this.articleId })
  }
}
